import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-card-result',
  templateUrl: './card-result.component.html',
  styleUrls: ['./card-result.component.scss']
})
export class CardResultComponent implements OnInit {

  @Output() showDetailsEmit = new EventEmitter();

  emit(showDetails, showMoreResults) {
    let options = {
      showDetails: showDetails,
      showMoreResults: showMoreResults,
    }
    console.log(options, 'options')
    setTimeout(() => {
      this.showDetailsEmit.emit(options)
    }, 200);
  }

  constructor() { }

  ngOnInit() {
  }

}
