import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/fr';

@Component({
  selector: 'app-side-booking',
  templateUrl: './side-booking.component.html',
  styleUrls: ['./side-booking.component.scss']
})
export class SideBookingComponent implements OnInit {

  constructor() { }

  @Output() showElement = new EventEmitter();

  showResults = false;
  resultsDay = false;
  resultsCar = false;
  showMoreResults = false;

  emit(showResults, resultsDay, resultsCar, showMoreResults) {
    showResults = !showResults
    let navigationOption = {
      showResults: showResults,
      resultsDay: resultsDay,
      resultsCar: resultsCar, 
      showMoreResults: showMoreResults
    }
    
    console.log(navigationOption, 'navigationOption')
    setTimeout(() => {
      this.showElement.emit(navigationOption)
    }, 200);
  }

  ngOnInit() {
  }

  date = new Date();
  carName;
  timeFrom;
  timeTo;

  carData = [
    { car: 'BMW' },
    { car: 'Audi' },
    { car: 'Lamborghini' },
    { car: 'Porshe' },
  ];


  timeDate = [
    { time: '09:00 PM' },
    { time: '09:30 PM' },
    { time: '10:00 PM' },
    { time: '10:30 PM' },
    { time: '11:00 PM' },
    { time: '11:30 PM' },
    { time: '12:00 PM' },
    { time: '12:30 PM' },
    { time: '13:00 PM' },
    { time: '14:00 PM' },
  ];


  options: DatepickerOptions = {
    minYear: 1970,
    maxYear: 2030,
    displayFormat: 'MMM D[,] YYYY',
    barTitleFormat: 'MMMM YYYY',
    dayNamesFormat: 'dd',
    firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
    locale: frLocale,
    minDate: new Date(Date.now()), // Minimal selectable date
    maxDate: new Date(Date.now()),  // Maximal selectable date
    barTitleIfEmpty: 'Click to select a date',
    placeholder: 'Click to select a date', // HTML input placeholder attribute (default: '')
    addClass: 'form-control', // Optional, value to pass on to [ngClass] on the input field
    addStyle: {}, // Optional, value to pass to [ngStyle] on the input field
    fieldId: 'my-date-picker', // ID to assign to the input field. Defaults to datepicker-<counter>
    useEmptyBarTitle: false, // Defaults to true. If set to false then barTitleIfEmpty will be disregarded and a date will always be shown 
  };



}
