import { Component, OnInit, TemplateRef, Output, EventEmitter } from '@angular/core';

import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/fr';

import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

@Component({
  selector: 'app-card-date-result',
  templateUrl: './card-date-result.component.html',
  styleUrls: ['./card-date-result.component.scss']
})
export class CardDateResultComponent implements OnInit {

  modalRef: BsModalRef;
  constructor(private modalService: BsModalService) { }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }

  date = new Date();

  timeFrom;
  timeTo;

  timeDate = [
    { time: '09:00 PM' },
    { time: '09:30 PM' },
    { time: '10:00 PM' },
    { time: '10:30 PM' },
    { time: '11:00 PM' },
    { time: '11:30 PM' },
    { time: '12:00 PM' },
    { time: '12:30 PM' },
    { time: '13:00 PM' },
    { time: '14:00 PM' },
  ];

  options: DatepickerOptions = {
    minYear: 1970,
    maxYear: 2030,
    displayFormat: 'MMM D[,] YYYY',
    barTitleFormat: 'MMMM YYYY',
    dayNamesFormat: 'dd',
    firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
    locale: frLocale,
    minDate: new Date(Date.now()), // Minimal selectable date
    maxDate: new Date(Date.now()),  // Maximal selectable date
    barTitleIfEmpty: 'Click to select a date',
    placeholder: 'Click to select a date', // HTML input placeholder attribute (default: '')
    addClass: 'form-control', // Optional, value to pass on to [ngClass] on the input field
    addStyle: {}, // Optional, value to pass to [ngStyle] on the input field
    fieldId: 'my-date-picker', // ID to assign to the input field. Defaults to datepicker-<counter>
    useEmptyBarTitle: false, // Defaults to true. If set to false then barTitleIfEmpty will be disregarded and a date will always be shown 
  };

  @Output() showDetailsEmit = new EventEmitter();

  emit(showDetails, showMoreResults) {
    let options = {
      showDetails : showDetails,
      showMoreResults : showMoreResults,
    }
    console.log(options, 'options')
    setTimeout(() => {
      this.showDetailsEmit.emit(options)
    }, 200);
  }

  ngOnInit() {
  }

}
