import { Component, OnInit } from '@angular/core';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/fr';

@Component({
  selector: 'app-pages',
  templateUrl: './pages.component.html',
  styleUrls: ['./pages.component.scss']
})
export class PagesComponent implements OnInit {

  showResults = false;
  resultsDay = false;
  resultsCar = false;
  showDetails = false;
  showMoreResults = false;
  userInfo = false;
  bookinHistory = false;
  myCar = false

  constructor() { }

  date = new Date();
  carName;
  timeFrom;
  timeTo;

  carData = [
    { car: 'BMW' },
    { car: 'Audi' },
    { car: 'Lamborghini' },
    { car: 'Porshe' },
  ];

  dataSlide = [
    { img: 'assets/img/car/sid-ramirez-736273-unsplash.png' },
    { img: 'https://storage.googleapis.com/gtspirit/uploads/2013/02/georgef458_08_w1023_h682.jpg' },
    { img: 'https://upload.wikimedia.org/wikipedia/commons/d/d0/2010-2011_Ferrari_458_Italia_coupe_%282011-03-23%29_03.jpg' },
  ];

  timeDate = [
    { time: '09:00 PM' },
    { time: '09:30 PM' },
    { time: '10:00 PM' },
    { time: '10:30 PM' },
    { time: '11:00 PM' },
    { time: '11:30 PM' },
    { time: '12:00 PM' },
    { time: '12:30 PM' },
    { time: '13:00 PM' },
    { time: '14:00 PM' },
  ];


  options: DatepickerOptions = {
    minYear: 1970,
    maxYear: 2030,
    displayFormat: 'MMM D[,] YYYY',
    barTitleFormat: 'MMMM YYYY',
    dayNamesFormat: 'dd',
    firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
    locale: frLocale,
    minDate: new Date(Date.now()), // Minimal selectable date
    maxDate: new Date(Date.now()),  // Maximal selectable date
    barTitleIfEmpty: 'Click to select a date',
    placeholder: 'Click to select a date', // HTML input placeholder attribute (default: '')
    addClass: 'form-control', // Optional, value to pass on to [ngClass] on the input field
    addStyle: {}, // Optional, value to pass to [ngStyle] on the input field
    fieldId: 'my-date-picker', // ID to assign to the input field. Defaults to datepicker-<counter>
    useEmptyBarTitle: false, // Defaults to true. If set to false then barTitleIfEmpty will be disregarded and a date will always be shown 
  };

  showUserInfo(){
    this.showResults = false;
    this.resultsDay = false;
    this.resultsCar = false;
    this.showDetails = false;
    this.showMoreResults = false;
    this.bookinHistory = false;
    this.userInfo = true;
    this.myCar = false;
  }
  showMyCar(){
    this.showResults = false;
    this.resultsDay = false;
    this.resultsCar = false;
    this.showDetails = false;
    this.showMoreResults = false;
    this.bookinHistory = false;
    this.userInfo = false;
    this.myCar = true;
  }
  showBookingHistory(){
    this.showResults = false;
    this.resultsDay = false;
    this.resultsCar = false;
    this.showDetails = false;
    this.showMoreResults = false;
    this.bookinHistory = true;
    this.userInfo = false;
    this.myCar = false;
  }

  closeMenuIsems(){
    this.bookinHistory = false;
    this.userInfo = false;
    this.myCar = false;
  }

  showElement(e){
    this.closeMenuIsems()
    this.showResults = e.showResults;
    this.resultsDay = e.resultsDay;
    this.resultsCar = e.resultsCar;
    this.showMoreResults = e.showMoreResults;
    console.log(e)
  }

  showDetailsEmit(e){
    this.closeMenuIsems()
    this.showDetails = e.showDetails;
    console.log(e)
  }

  ngOnInit() {
  }

}
